import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { ITile } from 'src/app/models/ITile';

@Component({
  selector: 'app-tile',
  templateUrl: './tile.component.html',
  styleUrls: ['./tile.component.css']
})
export class TileComponent implements OnInit {

  @Input() ImageUrl : string | ArrayBuffer;
  @Input() Tile : ITile;
  @Output() childEvent = new EventEmitter<Number>();

  ClassNameValue : string;

  get Top() : string {
    return this.Tile.top + "px";
  }

  get Left() : string {
    return this.Tile.left + "px";
  }

  ngOnInit(): void {
    this.ClassNameValue = `img img-${this.Tile.value}`;
    //console.log(this.Tile.id);
  }

  onChildClick() {
    this.childEvent.emit(this.Tile.id);
  }

}
