export interface ITile{
    id: number;
    value:number;
    top: number;
    left: number;
    isValid:()=> boolean;
    resetPost:(sideCnt : number, baseSize : number) => ITile;
}

export interface ITileList {
    [index: number] : ITile
}

export class Tile implements ITile {

    public top:number;
    public left:number;

    constructor(public id : number, public value : number) {
        //this.id = id;
        //this.value = value;
    }

    isValid() : boolean {
        return this.id + 1 == this.value;
    }

    resetPost(sideCnt : number, baseSize : number) : ITile {
        this.top = Math.floor(this.id / sideCnt);
        this.left = this.id % sideCnt;
        this.top = this.top * baseSize;
        this.left = this.left * baseSize;
        return this;
    }
}

export class TileTest {
    /**
     *
     */
    constructor(public id : number, public value : number) {
    }

    getSum() : number {
        return this.id + this.value;
    }
}

export const enum Direction {
    Up = 1,
    Down,
    Left,
    Right
}
